.container * {
    border: 1px white dashed;
}
.container{
    display: flex;
    justify-content: center;
}
.wrapper{
    display: block;
    max-width: 935px;
    width: 100%;
    padding: 20px 20px 0 20px;
    background: black;
}
.headerWrapper{
    display: flex;
    height: 200px;
}
.profilePictureContainer{
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
}
.profilePictureContainer img{
    border-radius: 150px;
    width: 150px;
    height: 150px;
}
.interactiveData{
    flex-grow: 4.34;
    background: green
}
.fullName{
    flex: 1 1 0;
    color: white;
    font-size: 20px;
}
.headerButtons{
    flex: 2 1 0;
}
.headerMain{;
    display: flex;
}
